.page__body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.page__header {
  margin: 0;
}

.page__section {
  margin: 0 auto;
  padding: 50px 40px;
}

.header {
  box-shadow: 0 2px 4px #0000000d;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.header__logo {
  max-height: 40px;
  text-decoration: none;
}

.nav {
  transition: all .3s linear;
}

.nav__list {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  justify-content: center;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item + .nav__item {
  margin-left: 20px;
}

.nav__linck {
  color: #060b35;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
}

.nav__linck--active {
  color: #00acdc;
  position: relative;
}

.nav__linck--active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__linck:hover {
  color: #00acdc;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  width: 200px;
  padding: 33px 17px 16px;
  transition: all .3s linear;
}

.card__foto {
  width: 160px;
  height: 134px;
}

.card__foto--position {
  margin: 0 3px 36px;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title--position {
  margin: 0 0 4px;
}

.card__code {
  color: #616070;
  font-size: 10px;
}

.card__code--position {
  margin: 0 0 17px;
}

.card__reviews {
  justify-content: space-between;
  display: flex;
}

.card__reviews--position {
  margin-bottom: 24px;
}

.card__review-count {
  color: #060b35;
  align-self: center;
  font-size: 10px;
  text-decoration: none;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__price--position {
  margin-bottom: 16px;
}

.card__money {
  font-size: 16px;
  font-weight: 700;
  line-height: inherit;
  color: #060b35;
}

.card__money--position {
  margin: 0;
}

.card__buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  justify-content: space-between;
  width: 96px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.section-card {
  grid-template-columns: repeat(auto-fill, 200px);
  justify-content: center;
  place-items: center;
  gap: 47px 48px;
  max-width: 944px;
  display: grid;
}

